
:root {
    --accent: #007acc
}

.print-builder body {}

h1 {
    margin: 0 0 8px
}

.layout {
    display: flex;
    gap: 18px;
    align-items: flex-start;
    flex-wrap: wrap;
}

.pane {
    flex: 1;
    border: 1px solid #ddd;
    border-radius: 8px;
    padding: 14px;
    min-width: 400px;
}

.controls {
    display: flex;
    gap: 8px;
    margin-top: 10px;
    justify-content: center;
}

button {
    padding: 8px 10px;
    border-radius: 6px;
    border: 1px solid #bbb;
    background: #fafafa;
    cursor: pointer
}

button.primary {
    background: var(--accent);
    color: #fff;
    border-color: #005a99
}

.meta {
    font-size: 13px;
    color: #555;
    margin-bottom: 10px
}

.question {
    font-weight: 600;
    margin-bottom: 10px
}

.choices {
    margin-bottom: 10px
}

.choice-line {
    margin: 6px 0
}

.stats {
    font-size: 13px;
    color: #333;
    margin-top: 8px
}

.selected-list {
    max-height: 10%;
    overflow: auto;
    border-top: 1px dashed #eee;
    padding-top: 8px;
    margin-top: 8px
}

.buttons-panel {
    margin-top: 10px;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    gap: 18px;
}

.small {
    font-size: 13px;
    color: #444
}

.notice {
    color: #b33;
    margin-top: 8px
}

#printPreview {
    display: flex;
    flex-wrap: wrap;
}

@media print {
    body {
        margin: 0
    }

    .no-print {
        display: none !important
    }

    .printarea {
        padding: 18px
    }
}
