.upload-json {
    border: 1px solid #e1e1e1;
    padding: 12px;
    border-radius: 8px;
    /* max-width: 680px; */
}

.upload-json h2 {
    margin: 0 0 8px;
    font-size: 18px;
}

.upload-json .hint {
    margin: 0 0 10px;
    color: #555;
    font-size: 13px;
}

.upload-json .controls {
    display: flex;
    gap: 8px;
    align-items: center;
    margin-bottom: 8px;
}

.upload-json input[type="file"] {
    font-size: 14px;
}

.upload-json .secondary {
    background: #f5f5f5;
    border: 1px solid #ccc;
    padding: 6px 10px;
    border-radius: 6px;
    cursor: pointer;
}

.upload-json .status {
    font-size: 13px;
    color: #222;
    margin-top: 6px;
}

.upload-json .message {
    margin-top: 6px;
    color: #0a5;
}